.Heading{
    font-size:30px;
    color: rgb(45, 108, 3);
    cursor: text;
    transition: all 0.2s ease-in-out;
    font-family:'montserrat';

}
.Heading:hover{
    color:rgb(11, 109, 11);
    transform: scale(1.05);
    letter-spacing: 2px;
}


.HeaderContainer{
    display:flex;
    margin-top:10px;
    
}
.navbar{
    margin-left:260px;
    font-size: 18px;
    padding-TOP:25PX;

}
.navbar a{
    padding:17px;
    color: rgb(45, 108, 3);
    text-decoration: none;
}

.AboutContainer{
    margin-top: 10px;
    width:1300px;
    height:700px;
    display: flex;

    

}

.ProjectContainer{
    margin-top: 10px;
    width:1300px;
    margin-bottom:50px;
    
    
}
.Project {
    border-radius: 10px;
    position: absolute ;
    border: 2px solid rgb(45, 108, 3);
    width: 320px;
    height: 350px;
    position: relative;
    overflow: hidden;
    margin-top: 10px;
    margin-right: 0px;
    background-color: rgb(255, 255, 255);
}
.projects-with-description{
    display: block;
    margin-right: 100px;
    
}
.project-description{
    display: block;
    width: 320px;
    
    text-align: justify;
    margin-top: 20px;
    margin-left:0px;
   
}

.projectHeading{
    font-size: 30px;
    margin-top:70px;
    margin-bottom: 0.2rem;
    color: #2d6c03;
    line-height: 0;
    display: block;
    font-family:'montserrat';
}

.allProjects{
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;
    margin-left: 0px;
    width: 100%;
    
    
}
.Project img {
    width: 100%;
    height: 75%;
    object-fit: cover;
    border-radius: 10px;
    transition: all 0.3s ease-in-out;
}
.Project p {
    font-size: 20px;
    font-family: 'montserrat';
    font-weight: 700;
    color: rgb(237, 125, 33);
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.7);
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
    text-align: center;
    margin: 0;
    padding: 10px;
    z-index: 2;
}
.github-icon {
    position: absolute;
    top: 10px;
    right: 10px;
    color: #2d6c03;
    font-size: 26px;
    background-color: white;
    border-radius: 50%;
    padding: 6px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
    z-index: 10;
    transition: transform 0.2s ease-in-out;
}

.github-icon:hover {
    transform: scale(1.2);
}

.Project a:hover svg {
    transform: scale(1.2);
    transition: all 0.2s ease-in-out;
  }
  
.Project img:hover {
    transform: scale(1.05);
    height: 99%;
    
    transition: all 0.2s ease-in-out;
}


.AboutHeading{
    font-size: 25px;
    margin-top:10px;
    margin-bottom: 0.2rem;
    line-height: 1.2; 
    display: block;
    font-family:'montserrat';
}
.para{
    font-size:25px;
    margin-top: 0px;
    width:  500px;
    font-family:'montserrat';
    text-align: justify;
}
.interest{
    font-size: 40px;
    color: rgba(27, 67, 1, 0.632);
    display: block;
    margin-top: 0px;
    
    text-align: left;
    height: 150px;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
   
}

.NameHeading{
    font-size:80px;
    margin-top: 0 ; 
    color: rgb(45, 108, 3);
    cursor: text;
    display: block;
    transition: all 0.2s ease-in-out;
    font-family:'montserrat';
}

.SkillTitleArea{
    font-size: 15px;
    margin-bottom: 10px;
    
}
.SkillArea{
    display: flex;
    color: aliceblue;
    
    
}
.SkillBox{
    margin-top: 20px;
}
.Skill {
    width: 100px;
    font-size: 15px;
    
    margin-right: 10px;
    border-radius: 7px;
    border: 1px solid black;
    display: flex; /* Enables flexbox */
